import Vue from 'vue'
import Router from 'vue-router'
import MainComp from '@/components/main'
import BrandComp from '@/components/brand'
import LandmarkComp from '@/components/landmark'
import GalleryComp from '@/components/gallery'
import NewsComp from '@/components/news'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/main',
      name: 'mainComp',
      component: MainComp
    },
    {
      path: '/brand',
      name: 'brandComp',
      component: BrandComp
    },
    {
      path: '/landmark',
      name: 'landmarkComp',
      component: LandmarkComp
    },
    {
      path: '/gallery',
      name: 'galleryComp',
      component: GalleryComp
    },
    {
      path: '/news',
      name: 'newsComp',
      component: NewsComp
    },
  ],
  scrollBehavior (to, from, savedPosition) {
    behavior: 'auto'
    return { x: 0, y: 0 }
  }
})



